* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family:'Poppins', sans-serif;
    font-size: 16px;
}

body {
    width: 100%;
    min-height: 100vh;
    background: url('../../assets/bg-intro-desktop.png') hsl(0, 100%, 74%);
}

main {
    width: 1100px;
    height: 600px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -300px;
    margin-left: -500px;
    display: flex;
}

#page-text {
    flex-basis: 45%;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#page-text h1 {
    font-size: 3rem;
    line-height: 1.12;
    font-weight: 700;
    margin-bottom: 1.5rem;
}

#page-text p {
    font-weight: 400;
}

@media (max-width: 1045px) {
    html {
        font-size: 18px;
    }
    
    main {
        width: 700px;
        height: 400px;
        margin-top: -200px;
        margin-left: -350px;
    }

    #page-text {
        flex-basis: 50%;
    }

    #page-text h1 {
        font-size: 2rem;
        margin-bottom: 1.2rem;
    }

    #page-text p {
        font-size: 0.7rem;
    }
}

@media (max-width: 720px) {
    body {
        background: url('../../assets/bg-intro-mobile.png') hsl(0, 100%, 74%);
    }

    main {
        flex-direction: column;
        width: 425px;
        min-height: 100vh;
        margin-left: calc(-425px / 2);
        top: 0;
        margin-top: 0;
    }

    #page-text {
        text-align: center;
    }

    #page-text h1 {
        margin-top: 1rem;
    }

    #page-text p {
        font-size: 0.75rem;
        margin-bottom: 1.5rem;
    }

}

@media (max-width: 425px) {
    main {
        width: 350px;
        margin-left: -175px;
    }

    #page-text h1 {
        font-size: 1.5rem;
        line-height: 1.2;
    }

    #page-text p {
        font-size: 0.8rem;
        width: 85%;
        align-self: center;
    }
}


@media (max-width: 350px) {
    main {
        width: 280px;
        margin-left: -140px;
    }
} 